.MainArea,
.Sidebar {
  padding: 10px;
}
.Sidebar {
  box-sizing: border-box;
  position: relative;
  .List {
    margin-bottom: 10px;
    .filterInputWrapper {
      &>input {
        width: 100%;
      }
    }
    .SideNav {
      overflow-y: auto;
      height: 400px;
    }
    .currentTab {
      font-weight: bold;
    }
  }
  .Map {
    cursor: pointer;
    .esri-view {
      height: 400px;
    }
  }
  .SuggestLinkWrapper {
    position: absolute;
    top: 0px;
    right: 0px;
  }
}

.MainArea {
  height: 100%;
  box-sizing: border-box;
  .Tabs {
    .TabsHeaders {
    }
    .iframeWrapper {
      height: 600px;
      .mainIframe {
        width: 100%;
        border: 0px;
        height: 100%;
      }
    }
    .LinkPanel {
    }
    .noPanelMessage {
      font-size: 20px;
    }
  }
}
.LoadingDiv {
  padding: 10px;
}

// Mobile first. The following styles will be applied on large screens only.
@media (min-width: 768px) {
  html,
  body,
  #root,
  .App,
  .split {
    margin: 0;
    height: 100%;
  }
  .Sidebar {
    display: flex;
    flex-flow: column;
    height: 100%;
    overflow: hidden;
    .List {
      flex-grow: 1;
      display: flex;
      flex-flow: column;
      .Panel {
      }
      .SideNav {
        flex-basis: 0;
        flex-grow: 1;
      }
      .currentTab {
      }
    }
    .Map {
      flex-basis: 0;
      flex-shrink: 1;
      flex-grow: 1;
      height: 100%;
      .esri-view {
        height: 100%;
      }
    }
  }

  .MainArea {
    .Tabs {
      display: flex;
      flex-flow: column;
      height: 100%;
      overflow: hidden;
      .TabsHeaders {
      }
      .iframeWrapper {
        // this works in IE too but it feels like a hack, maybe because TabsHeaders has flex from calcite?
        flex-grow: 99;

        // The following four rules are to support centering vertically the text when there is no content to show:
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;

        .mainIframe {
        }
      }
      .LinkPanel {
      }
      .noPanelMessage {
      }
    }
  }
}
